.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -10000;
}
.quote {
  color: white;
  position: absolute;
  bottom: 5vh;
  left: 0;
  width: 100vw;
}
.quote-content {
  font-weight: bold;
}
.quote-author {
  padding-top: 5px;
  font-weight: bold;
  font-size: 70%;
}
